.footer{
    width: 100%;
    height: 150px;
    background-color: rgba(0, 0, 0, 0.192);
    backdrop-filter: blur(8px);
    color: rgb(218, 218, 218);
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 0px;
    font-size: 12px;
    text-align: left;
    z-index: 2;
}

.footer .h-card{
    padding-left: 15px;
}

.h-card .e-mail{
   color: rgb(218, 218, 218);
}

.h-card svg{
    cursor: pointer;
    text-decoration: none;
    color: rgb(218, 218, 218);
    font-size: 15px;
}